@use "function" as *;
// -----------------------------
//  @mixin
//------------------------------


// 画像置換
@mixin replace($w: 0, $h: 0, $display: block, $position: left) {
  display: #{$display};
  overflow: hidden;

  @if $w !=0 {
    width: #{$w};
    height: #{$h};
  }

  white-space: nowrap;
  font-size: 0;
  text-indent: 100%;

  @if $position ==center {
    margin: auto;
  }
}

// 擬似要素
@mixin pseudo($w: 0, $h: 0, $direction: left, $directionPosition1: 0, $directionPosition2: 0, $directionPosition3: 0, $directionPosition4: 0) {
  content: "";
  display: block;
  position: absolute;
  margin: auto;

  @if $direction ==left {
    left: #{$directionPosition1};
    top: #{$directionPosition2};
    bottom: #{$directionPosition3};

  }

  @else if $direction ==right {
    right: #{$directionPosition1};
    top: #{$directionPosition2};
    bottom: #{$directionPosition3};

  }

  @else if $direction ==center {
    top: #{$directionPosition1};
    right: #{$directionPosition2};
    bottom: #{$directionPosition3};
    left: #{$directionPosition4};
  }

  @if $w !=0 {
    width: #{$w};
    height: #{$h};
  }
}

// media query
@mixin max-screen($breakpoint) {
  @media (max-width: $breakpoint) {
    @content;
  }
}

@mixin min-screen($breakpoint) {
  @media (min-width: $breakpoint) {
    @content;
  }
}

@mixin screen($breakpoint-min, $breakpoint-max) {
  @media (min-width: $breakpoint-min) and (max-width: $breakpoint-max) {
    @content;
  }
}

@mixin breakpoint-up($name) {
  $min: breakpoint-min($name);

  @if $min {
    @media (min-width: $min) {
      @content;
    }
  }

  @else {
    @content;
  }
}

@mixin breakpoint-down($name) {
  $max: breakpoint-max($name);

  @if $max {
    @media (max-width: $max) {
      @content;
    }
  }

  @else {
    @content;
  }
}

@mixin breakpoint-between($lower, $upper) {
  $min: breakpoint-min($lower);
  $max: breakpoint-max($upper);

  @if $min !=null and $max !=null {
    @media (min-width: $min) and (max-width: $max) {
      @content;
    }
  }

  @else if $max ==null {
    @include breakpoint-up($lower) {
      @content;
    }
  }

  @else if $min ==null {
    @include breakpoint-down($upper) {
      @content;
    }
  }
}

@mixin breakpoint-only($name) {
  $min: breakpoint-min($name);
  $max: breakpoint-max($name);

  @if $min !=null and $max !=null {
    @media (min-width: $min) and (max-width: $max) {
      @content;
    }
  }

  @else if $max ==null {
    @include breakpoint-up($name) {
      @content;
    }
  }

  @else if $min ==null {
    @include breakpoint-down($name) {
      @content;
    }
  }
}


// 文字列置換
@function str-replace($substr, $newsubstr, $str, $all: false) {
  $pos : str-index($str, $substr);

  @while $pos !=null {
    $strlen : str-length($substr);
    $start : str-slice($str, 0, $pos - 1);
    $end : str-slice($str, $pos + $strlen);
    $str : $start + $newsubstr + $end;

    @if $all ==true {
      $pos : str-index($str, $substr);
    }

    @else {
      $pos : null;
    }
  }

  @return $str;
}

@function webp-replace($str) {
  @if (str-index($str, ".png")) {
    @return str-replace(".png", ".webp", $str);
  }

  @else {
    @if (str-index($str, ".jpg")) {
      @return str-replace(".jpg", ".webp", $str);
    }

    @else {
      @if (str-index($str, ".gif")) {
        @return str-replace(".gif", ".webp", $str);
      }

      @else {
        @return $str;
      }
    }
  }
}

// 背景指定
@mixin bg($url, $position: 0% 0%, $size: auto auto, $repeat: repeat, $color: transparent, $lazy: null) {
  background-color: $color;

  @if not $lazy {
    &.lazyloaded {
      background-image: url(webp-replace($url));

      .no-webp & {
        background-image: url($url);
      }
    }

    @at-root .lazyloaded {

      &::before,
      &::after {
        background-image: url(webp-replace($url));

        .no-webp & {
          background-image: url($url);
        }
      }
    }
  }

  @else {
    background-image: url(webp-replace($url));

    .no-webp & {
      background-image: url($url);
    }

    @at-root {

      &::before,
      &::after {
        background-image: url(webp-replace($url));

        .no-webp & {
          background-image: url($url);
        }
      }
    }
  }

  background-repeat: $repeat;
  // background-attachment: $attachment;
  background-position: $position;
  background-size: $size;
}

@mixin bg-image($url, $lazy: null) {
  @if not $lazy {
    &.lazyloaded {
      background-image: url(webp-replace($url));

      .no-webp & {
        background-image: url($url);
      }
    }

    @at-root .lazyloaded {

      &::before,
      &::after {
        background-image: url(webp-replace($url));

        .no-webp & {
          background-image: url($url);
        }
      }
    }
  }

  @else {
    background-image: url(webp-replace($url));

    .no-webp & {
      background-image: url($url);
    }

    @at-root {

      &::before,
      &::after {
        background-image: url(webp-replace($url));

        .no-webp & {
          background-image: url($url);
        }
      }
    }
  }
}

@mixin hover() {
  @media (any-hover: hover) {
    &:hover {
      @content;
    }
  }
}

@mixin not-hover() {
  @media (any-hover: hover) {
    &:not(:hover) {
      @content;
    }
  }
}